#footer {
  width: 100%;
  display: inline-block;
  margin:0 auto;
  background:#355172 !important;
  color:#DFEEF5;
  padding-bottom: 3rem;
}
.mid-footer{
  text-align: center;
}
.logo2{
  width: 15rem;
  height: 10rem;
}
.telfo{
  color: red !important;
}
p{

  font-size: 1rem;
  font-weight: 500;
}
#footer ul li a{
 
  text-align: left;
  color:#5F9EAD;}

  ul{
    padding: 0px !important;
    list-style-type: none;
    list-style: none;
  }
#footer 
.lh-footer {
  width:33%;
  display: inline-block;
 
}

.mid-footer {
  margin-left:50px;
  width:33%;
  display: inline-block;
}

.rh-footer {
  width: 23%;
  display: inline-block;
}

@media (max-width:1000px) {
  #footer{max-width:950px;}
}

@media (max-width:800px) {
  .lh-footer, .mid-footer,  .rh-footer{display:block; width:90%; margin:0 auto;}
}

@media screen and (max-width:681px) {
 
  #footer .lh-footer {
    display: flex;
    align-items: center;
    width: 60%;
    flex-direction: column;}
}
  @media screen and (max-width:355px) {
    #footer .lh-footer {
        width: 70%;
    }}
    @media screen and (max-width:285px) {
      #footer .lh-footer {
          width: 80%;
      }}